export default {
	data() {
		return {
			tab: [{
					name: '公司介绍',
					id: 0,
				},
				{
					name: '公司资质',
					id: 1,
				},
				{
					name: '相关产品',
					id: 2,
				},
				{
					name: '相关案例',
					id: 3,
				},
				
			],
			tabid:2,
			showIntro:false,
			showAble:false,
			showProduct:true,
			showCase:false,
			
			product:[
				
			],
			cases:[
				
			],
		}
	},
	onLoad() {
		this.dataReq();
	},
	onShow() {
		
	},
	methods: {
		changeTab(id){
			this.tabid = id;
			// console.log(id);
			
			if(this.tabid == 0){
				this.showIntro = true;
				this.showAble =false;
				this.showProduct = false;
				this.showCase = false;
			}
			else if(this.tabid == 1){
				this.showIntro = false;
				this.showAble =true;
				this.showProduct = false;
				this.showCase = false;
			}else if(this.tabid == 2){
				this.showIntro = false;
				this.showAble =false;
				this.showProduct = true;
				this.showCase = false;
			}else if(this.tabid == 3){
				this.showIntro = false;
				this.showAble =false;
				this.showProduct = false;
				this.showCase = true;
			}
		},
		productDetail(uid){
			console.log(uid);
			uni.navigateTo({
			      url: '/pages/detail/detail?uid=' + uid,
			})
		},
		async dataReq(){
			var data = "";
			var res = await this.$request.get('http://127.0.0.1:5000/product',);
			// console.log(res);
			this.productQuery(res.data);
		},
		productQuery(data){
			console.log(data);
			for(var i = 0 ; i < data.length; i ++){
				if(data[i].uid == " " || data[i].uid == null){
					this.dataReq();
				}else{
					if(data[i].type == 0){
						this.product.push(data[i]);
						// console.log(this.product);
					}else if(data[i].type == 1){
						this.cases.push(data[i]);
						// console.log(this.cases);
					}
				}
				
			}
		}
	},
}
